.our {
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(slider) {
    padding-bottom: rem(50px);
  }
  @include e(slide) {
    padding: rem(20px) rem(20px) 0;
    text-align: center;
    @include border(1px, $mercury);
  }
  @include e (info) {
    text-align: center;
  }
  @include e (title) {
    color: $shark;
  }
  @include e(icon) {
    margin-bottom: rem(25px);
  }
  @include e(name) {
    color: $shark;
    text-transform: uppercase;
    margin-bottom: rem(5px);
  }
  @include e(undertitle) {
    color: $shark;
    @include font-size(18, 21);
    font-weight: $medium;
    margin-bottom: rem(30px);
  }
  @include e(text) {
    color: $shark;
    margin-bottom: rem(10px);
  }
  @include e(btn) {
    @include border-t(1px, $mercury);
    padding: rem(15px) 0;
    @include flex-sb();
  }
  @include e(read) {
    text-transform: capitalize;
    color: $shark;
  }
  @include e(img) {
    width: rem(25px);
    @include img();
  }
}

