.fixed-header {
  z-index: 5;
  background: $curiousBlue;
  @include transition(0.3);
}

.btn {
  display: inline-block;
  background: $curiousBlue;
  color: $white;
  text-transform: uppercase;
  font-size: rem(14px);
  line-height: 1;
  padding: rem(14px) rem(20px);
}

.title {
  @include before() {
    height: rem(2px);
    width: 100%;
    background: $curiousBlue;
    right: 0;
    bottom: rem(-7px);
    margin: auto;
  }
  display: inline-block;
  text-transform: uppercase;
  color: $white;
  font-size: #{clamp-size(24px, 36px, 320px, 1146px)};
  margin-bottom: #{clamp-size(30px, 50px, 320px, 1146px)};
  font-weight: $bold;
  .blue {
    color: $curiousBlue;
  }
}

.text {
  color: $white;
  @include font-size(14, 22);
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="file"],
input[type="number"],
input[type="date"],
textarea {
  display: inline-flex;
  width: 100%;
  padding: rem(15px);
  @include border-b(1px, $geyser);
  @include font-size(15, 20);
  color: $white;
}

input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  color: $white;
}

textarea {
  height: rem(150px);
  resize: none;
  outline: none;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: #{clamp-size(20px, 50px, 320px, 1146px)};
  color: $white;
  opacity: 0.7;
}

.swiper-button-next {
  right: rem(-7px);
  width: rem(50px);
}

.swiper-button-prev {
  left: rem(-7px);
  width: rem(50px);
}

.swiper-pagination-bullet {
  width: rem(30px);
  height: rem(6px);
  border-radius: rem(3px);
  background: transparent;
  @include border(2px, $curiousBlue);
}

.swiper-pagination-bullet-active {
  background: $curiousBlue;
}

.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: rem(12px);
}

.nav__btn:hover {
  background: transparent !important;
  color: $white !important;
  opacity: 1;
}