.blog {
  background: $shark;
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(info) {
    text-align: center;
  }
  @include e(items) {
    color: $white;
    text-align: center;
  }
  @include e(item) {
    @include notlc() {
      margin-bottom: rem(24px);
    }
  }
  @include e(img) {
    img {
      -webkit-filter: grayscale(1);
    }
    @include transition(0.3);
    @include after() {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      @include border(10px, $curiousBlue);
      @include transition(0.3);
    }
    @include img();
    margin-bottom: rem(15px);
  }
  @include e (box) {
    padding: rem(16px);
  }
  @include e(date) {
    margin-bottom: rem(25px);
  }
  @include e(description) {
    @include font-size(18, 26);
    font-weight: $medium;
    margin-bottom: rem(15px);
  }
  @include e(text) {
    margin-bottom: rem(15px);
  }
  @include e(btn) {
    text-transform: capitalize;
    color: $white;
    @include flex-c();
    gap: rem(10px);
  }
}
