.get {
    background: url("../img/touch/bg.png") no-repeat center;
    background-size: cover;
    @include e (container) {
        @include flex-sb();
        flex-direction: row;
        padding-bottom: 0;
    }
    @include e (left) {
        order: 0;
        flex: 0 0 50%;
    }
    @include e (right) {
        flex: 0 0 40%;
    }
    @include e (form) {
        padding: rem(40px);
        background: $shark;
    }
}