.team {
  background: $darkShark url("../img/team/text.png") no-repeat center;
  background-size: contain;
  background-attachment: fixed;
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(info) {
    text-align: center;
    margin-bottom: rem(50px);
  }
  @include e(item) {
    position: relative;
    padding: 0 rem(20px) rem(10px);
    @include img();
    margin-bottom: rem(30px);
    img {
        position: relative;
        z-index: 2;
    }
    @include before() {
      width: 100%;
      height: 90%;
      left: 0;
      bottom: 0;
      @include border(10px, $curiousBlue);
      @include transition(0.3);
    }
  }
  @include e (name) {
    text-align: center;
    @include font-size (38, 45);
    color: $white;
    font-weight: $medium;
    text-transform: capitalize;
    position: absolute;
    left: 50%;
    bottom: rem(-24px);
    transform: translateX(-50%);
    z-index: 5;
    opacity: 0;
    overflow: hidden;
    @include transition(0.3);
  }
}
