.footer {
  background: $darkShark;
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(col) {
    color: $white;
    @include m(third) {
      display: none;
    }
    margin-bottom: rem(20px);
  }
  @include e(logo) {
    @include center-block();
    display: block;
    max-width: rem(140px);
    margin-bottom: rem(20px);
  }
  @include e(text) {
    text-align: center;
    color: $white;
    @include font-size(16, 33);
    margin-bottom: rem(40px);
  }
  @include e(name) {
    @include after() {
      content: "\f107";
      font-family: "Font Awesome 5 Free";
      font-size: rem(20px);
      color: $curiousBlue;
      right: rem(23px);
      @include transition(0.3);
    }
    &.active {
      @include after() {
        @include transition(0.3);
        content: "\f107";
        transform: rotate(90deg);
        right: 0;
        bottom: rem(29px);
      }
    }
    text-transform: capitalize;
    @include font-size(24, 36, 1);
    font-weight: $semi;
    margin-bottom: rem(20px);
  }
  @include e(list) {
    padding-left: rem(20px);
    overflow: hidden;
    max-height: 0;
    @include transition(0.3);
  }
  @include e(link) {
    color: $white;
    @include font-size(16, 30);
  }
  @include e(contacts) {
    overflow: hidden;
    max-height: 0;
    @include transition(0.3);
  }
  @include e (copyright) {
    text-align: center;
    padding: rem(15px) 0;
    color: $white;
    background: $black;
  }
}

.social {
  @include flex-c();
  gap: rem(18px);
  @include e(link) {
    color: $white;
  }
}

.post {
  @include e(img) {
    @include img();
  }
}

.contacts {
  @include e(item) {
    @include flex();
    margin-bottom: rem(30px);
  }
  @include e(icon) {
    align-self: flex-start;
  }
  @include e(link) {
    display: block;
    color: $white;
    @include notlc () {
      margin-bottom: rem(15px);
    }
  }
  @include e(num) {
    padding-left: rem(25px);
  }
  @include e(icon) {
    color: $curiousBlue;
    font-size: rem(20px);
  }
}
