.pricing {
  background: $shark;
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(info) {
    text-align: center;
  }
  @include e(item) {
    @include bg-opacity($black, 0.12);
    text-align: center;
    color: $white;
    padding: rem(20px);
    @include borderopacity(1px, $white, 0.25);
    @include notlc() {
      margin-bottom: rem(25px);
    }
  }
  @include e(img) {
    display: none;
  }
  @include e(name) {
    text-transform: capitalize;
    @include font-size(20, 24);
    font-weight: $bold;
    margin-bottom: rem(8px);
  }
  @include e(undertitle) {
    margin-bottom: rem(27px);
  }
  @include e(text) {
    @include font-size(16, 30);
    margin-bottom: rem(20px);
  }
  @include e(price) {
    @include font-size(48, 58);
    font-weight: $bold;
    margin-bottom: rem(20px);
  }
  @include e(btn) {
    width: 100%;
    background: $white;
    color: $shark;
  }
}
