.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  @include e(container) {
    padding-top: rem(30px);
    padding-bottom: rem(30px);
  }
}

.nav {
  @include flex-sb();
  @include e(list) {
    position: absolute;
    z-index: 50;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background: $curiousBlue;
    @include flex-c();
    flex-direction: column;
    @include transition(0.2);
    &.active {
      left: 0;
    }
  }
  @include e(link) {
    text-transform: uppercase;
    color: $white;
    font-size: rem(20px);
    line-height: 2;
  }
  @include e(btn) {
    display: none;
  }
  @include e(burger) {
    @include burger();
  }
}
