.banner {
  @include bg-img("../img/slider/bg.jpg", center);
  @include e(img) {
    img {
      height: #{clamp-size(320px, 600px, 320px, 1146px)};
      vertical-align: top;
    }
  }
  @include e(container) {
    padding-top: rem(90px);
  }
  @include e(slider) {
    text-align: center;
  }
  @include e(inner) {
    display: block;
    position: relative;
  }
  @include e(info) {
    position: absolute;
    left: 0;
    right: 0;
    bottom: rem(60px);
  }
  @include e(name) {
    text-transform: uppercase;
    color: $white;
    font-weight: $bold;
    font-size: #{clamp-size(32px, 60px, 320px, 1146px)};
    @include text-shadow(0px, 3px, 8px, rgba(52, 52, 52, 0.25));
  
  }
}
