.footer {
  @include e(container) {
    flex-wrap: nowrap;
    gap: rem(25px);
  }
  @include e(col) {
    @include nth-child(1) {
      flex: 0 0 25%;
      margin-bottom: 0;
    }
    @include nth-child(2) {
        flex: 0 0 15%;
    }
  }
  @include e(text) {
    text-align: left;
  }
  @include e(logo) {
    margin-left: 0;
  }
}

.social {
  justify-content: left;
}
