.get {
  background: $darkShark;
  @include e(container) {
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
    @include flex();
    flex-direction: column;
  }
  @include e(left) {
    order: 1;
  }
  @include e(img) {
    @include img();
  }
  @include e(right) {
    margin-bottom: rem(30px);
  }
  @include e(info) {
    text-align: center;
  }
}

.form {
  text-align: center;
  @include e(title) {
    text-transform: uppercase;
    color: $white;
    @include font-size(22, 27, 2);
    font-weight: $bold;
    margin-bottom: rem(30px);
  }

  @include e(textarea) {
    @include font-size(15, 20);
    padding: rem(15px);
    @include border-b(1px, $geyser);
    margin-bottom: rem(20px);
  }
}
