.footer {
  @include e(container) {
    @include flex-sb();
    flex-wrap: wrap;
  }

  @include e(col) {
    align-self: flex-start;
    margin-bottom: 0;
    @include nth-child(1) {
      flex: 1 0 100%;
      margin-bottom: rem(30px);
    }
    @include m(third) {
      display: block;
    }
    @include e(post) {
    }
  }
  @include e(list) {
    padding-left: 0;
    overflow: visible;
    max-height: 100%;
  }
  @include e(contacts) {
    overflow: visible;
    max-height: 100%;
  }
  @include e(name) {
    @include after() {
      content: none;
    }
  }
}

.post {
  @include flex();
  margin-bottom: rem(20px);
  color: $white;
  @include e(img) {
    width: rem(60px);
  }
  @include e(news) {
    padding-left: rem(25px);
  }
}
