.about {
  background: $shark;
  @include e(container) {
    @include flex();
    flex-direction: column;
    padding-top: #{clamp-size(40px, 80px, 320px, 1146px)};
    padding-bottom: #{clamp-size(40px, 80px, 320px, 1146px)};
  }
  @include e(left) {
    @include img();
    order: 1;
  }

  @include e(right) {
    text-align: center;
    margin-bottom: rem(50px);
  }
  @include e(text) {
    margin-bottom: rem(16px);
  }
}
