.nav {
    @include e(link) {
      @include transition(0.3);
      @include after() {
        width: 0;
        height: rem(2px);
        background: $white;
        left: 0;
        bottom: rem(-3px);
        @include transition(0.3);
      }
      @include hover() {
        opacity: 1;
      }
    }
  }
  
  .nav__link:hover::after {
    width: 100%;
  }
  
  .btn {
    @include transition(0.3);
    @include hover() {
      background: $white;
      color: $shark;
    }
  }
  
  .team {
    @include e(item) {
      @include hover() {
        @include before() {
          background: $curiousBlue;
        }
      }
    }
  }
  
  .team__item:hover .team__name {
    opacity: 1;
    overflow: visible;
  }
  
  .pricing {
    @include e(item) {
      @include transition(0.3);
      @include hover() {
        background: $white;
        color: $shark;
      }
    }
  }
  
  .pricing__item:hover .pricing__btn {
    background: $curiousBlue;
    color: $white;
  }
  
  .pricing__item:hover .pricing__undertitle {
    color: $shark;
  }
  
  .our {
    @include e(slide) {
      @include transition(0.3);
      @include hover() {
        background: $curiousBlue;
      }
    }
  }
  
  .our__slide:hover .our__name {
    color: $white;
  }
  
  .our__slide:hover .our__undertitle {
    color: $white;
  }
  
  .our__slide:hover .our__text {
    color: $white;
  }
  
  .our__slide:hover .our__read {
    color: $white;
  }
  
  .our__slide:hover .our__icon {
    --fill-icon: #242529;
  }
  
  .blog {
    @include e(item) {
      @include transition(0.3);
      @include hover() {
        background: $curiousBlue;
      }
    }
  }
  
  .blog__item:hover img {
    -webkit-filter: grayscale(0);
  }
  
  .blog__item:hover .blog__img::after {
      border: 10px solid transparent;
  }