.nav {
  @include flex-sb();
  @include e (content) {
    @include flex-sb();
    gap: rem(30px);
  }
  @include e(list) {
    width: auto;
    height: auto;
    display: flex;
    gap: rem(30px);
    flex-direction: row;
    background: transparent;
    position: static;
  }
  @include e(link) {
    font-size: rem(14px);
    line-height: 1;
    opacity: 0.7;
  }
  @include e(burger) {
    display: none;
  }
  @include e(btn) {
    background: transparent;
    @include borderopacity(1px, $white, 0.7);
    opacity: 0.7;
    display: block;
  }
}
